import React from "react";
import { canvasWheel } from "../../../../../_actions/player.actions";
import { IMelodition } from "../../../../../_types/player.types";

interface CanvasProps {
  Melo: IMelodition;
  drawGame: (canvas: HTMLCanvasElement) => void;
}
const GameFront: React.FC<CanvasProps> = ({ Melo, drawGame }) => {
  const drawFrontContext = (ref: HTMLCanvasElement | null) => {
    if (ref && Melo.gameLoading && Melo.canvasSize) {
      drawGame(ref);
    }
  };
  return (
    <canvas
      ref={(ref) => drawFrontContext(ref)}
      width={Melo.canvasSize?.width}
      height={Melo.canvasSize?.height}
      style={{ position: "absolute", top: 0, left: 0, zIndex: 2 }}
      onWheel={(e) => canvasWheel(e)}
    />
  );
};
export default GameFront;
