import React, { useEffect } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { gamePiano, notePiano } from "../../../../_actions/player.actions";
import { leftLine, PianoEvent, pitchName, rightLine } from "./constant";
import { makeStyles, Theme } from "@material-ui/core";
import { changeMidi } from "../../../../_actions/midi.actions";
import { INotePos } from "../../../../_types/player.types";
import { AppActions, AppState } from "../../../../_types";
import SvgKeys from "./SvgKeys";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "flex",
    flexDirection: "row-reverse",
    justifyContent: "center",
    flexWrap: "nowrap",
    margin: "0 auto",
    minWidth: "215px",
    width: "100%",
    cursor: "default",
    "-webkit-touch-callout": "none",
    "-webkit-user-select": "none",
    "-khtml-user-select": "none",
    "-moz-user-select": "none",
    "-ms-user-select": "none",
    "user-select": "none",
    touchAction: "none",
  },
  back: {
    margin: theme.spacing(0, 2),
  },
}));

type Props = ReturnType<typeof mapStateToProps> &
  ReturnType<typeof mapDispatchToProps>;

const Piano: React.FC<Props> = (props) => {
  const { /*theme,*/ Melo, drawGamePiano, drawNotePiano } = props;

  const {
    keyboardDown,
    keyboardUp,
    mouseDown,
    mouseUp,
    mouseLeave,
    mouseOver,
  } = new PianoEvent();

  let counter = 36;
  let firstkey = 36;
  const keyRefs = {} as SVGGElement[];

  useEffect(() => {
    console.log("Piano mounted");
    changeMidi(true);
    document.addEventListener("keydown", keyboardDown);
    document.addEventListener("keyup", keyboardUp);
    return () => {
      console.log("Piano disMounted");
      changeMidi(false);
      document.removeEventListener("keydown", keyboardDown);
      document.removeEventListener("keyup", keyboardUp);
    };
  }, [Melo.loading, keyboardDown, keyboardUp]);

  const classes = useStyles();

  const counterFunction = (octSize: number) => {
    if (octSize > 6) {
      counter = 24;
      firstkey = 24;
    } else if (octSize > 3) {
      counter = 36;
      firstkey = 36;
    } else if (octSize > 1) {
      counter = 48;
      firstkey = 48;
    } else {
      counter = 60;
      firstkey = 60;
    }
  };
  const detectSize = (height: number) => {
    const rythm = Melo.bpm * 4;
    let tap = 32;
    if (height > 700) {
      tap = height / rythm;
    } else if (height > 500) {
      tap = height / rythm;
    } else if (height > 300) {
      tap = height / rythm;
    } else if (height > 100) {
      tap = height / rythm;
    } else {
      tap = height / rythm;
    }
    return tap;
  };
  const setKeyRef = (ref: SVGGElement | null) => {
    if (ref) {
      const key = ref.tabIndex;
      keyRefs[key] = ref;
    }
  };

  const setRef = (ref: HTMLDivElement) => {
    if (ref && Melo.pianoLoading) {
      const pianoSize = ref.getBoundingClientRect();
      const width = pianoSize.width;
      const height = Math.floor(window.innerHeight - (pianoSize.height + 64));
      if (Melo.playMode) {
        const tapSize = detectSize(height);
        const baseLine = tapSize * (Melo.bpm <= 4 ? 4 : Melo.bpm) + 8;
        const pianoLeft = pianoSize.left;
        const canvasSize = { width, height, baseLine, pianoLeft };
        drawGamePiano(keyRefs, canvasSize, tapSize);
      } else if (Melo.noteMode && Melo.neededLines) {
        const need = Melo.neededLines.up > 83 ? 102 : 83;
        const lines =
          Melo.neededLines.down < 58 || Melo.neededLines.up > 83 ? 28 : 16;
        const lineSide = need > 83 ? rightLine : leftLine;
        const tapSize = height / 2.1 / (lines / 2);
        const spaceSize = width / (Melo.neededLines.notes + 2);
        const fontSize =
          spaceSize * 0.5 < 7 ? 7 : spaceSize * 0.5 > 12 ? 12 : spaceSize * 0.5;
        const linesArray = Array.from(
          Array(lines),
          (_, i) => need - lineSide[i]
        );
        const notePos: INotePos = {};
        linesArray.forEach((v, i) => {
          const y = ((i + 1) * tapSize) / 2;
          notePos[v] = { pos: y, note: pitchName(v) };
          notePos[v - 1] = { pos: y, note: pitchName(v - 1) };
        });
        drawNotePiano(keyRefs, {
          width,
          height: height / 2.1,
          tapSize,
          spaceSize,
          fontSize,
          linesArray,
          notePos,
        });
      } else {
        drawGamePiano(keyRefs);
      }
    }
  };
  const themeColor = (color: string) => {
    // if (color === "black") return theme === "dark" ? "white" : "black";
    // if (color === "white") return theme === "dark" ? "black" : "white";
    return color;
  };
  // const themer = theme === "dark" ? 1 : 0;
  counterFunction(Melo.octave);
  return (
    <div ref={setRef} className={classes.root}>
      {Array.from(Array(Melo.octave), (_, i) => "oct" + (i + 1)).map(
        (oct, i) => {
          const width = Melo.octave - i === 1 ? 668 : 584;
          return (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              id={oct}
              key={oct}
              width={width}
              height="100%"
              viewBox={`0 0 ${width} 275`}
              fill="none"
              style={
                i !== Melo.octave - 1
                  ? { marginRight: 8 / Melo.octave }
                  : undefined
              }
            >
              {SvgKeys.map((k, n) => {
                let lastKey = SvgKeys.length;
                let className = counter === firstkey ? "first" : k.note;
                if (Melo.octave - i === 1 && lastKey === n + 1) {
                  lastKey++;
                  className = "last";
                  i++;
                }
                let backFill = k.id === "white" ? "#B5B5B5" : undefined;
                let frontFill =
                  k.id === "white" ? "black" : "rgb(255 255 255 / 65%)";
                const active = Melo.neededTracks?.some(
                  (el) => el.pitch + 36 === counter
                );
                const textPos = k.id === "white" ? k.xPos : k.xPos + 6;
                return (
                  lastKey !== n + 1 && (
                    <g
                      id={k.id}
                      ref={setKeyRef}
                      onMouseUp={mouseUp}
                      onMouseDown={mouseDown}
                      onMouseLeave={mouseLeave}
                      onTouchEnd={mouseLeave}
                      onTouchMove={mouseOver}
                      onTouchStart={mouseDown}
                      key={oct + "-" + k.note + i}
                      className={className}
                      tabIndex={counter}
                    >
                      <path
                        id="back"
                        d={k.front}
                        fill={themeColor(k.id)}
                        stroke={backFill}
                      />
                      {k.id !== "white" ? (
                        <path
                          id="front"
                          d={k.back}
                          fill={themeColor(frontFill)}
                        />
                      ) : (
                        <text
                          id="front"
                          x={textPos}
                          y={k.yPos}
                          fill={themeColor("rgb(155 155 155)")}
                          fontSize="25"
                          fontWeight="900"
                        >
                          {k.note}
                        </text>
                      )}
                      {active && (
                        <circle
                          cx={k.xPos - 8}
                          cy={k.yPos + 20}
                          r="6"
                          fill={themeColor("rgb(56 56 56)")}
                          stroke={themeColor(k.id)}
                          strokeWidth="2"
                        />
                      )}
                      {counter++ === 60 && (
                        <polygon points="30,265 40,255 50,265" fill="black" />
                      )}
                    </g>
                  )
                );
              })}
            </svg>
          );
        }
      )}
    </div>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.auth.theme,
  Melo: state.melodition,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  drawGamePiano: bindActionCreators(gamePiano, dispatch),
  drawNotePiano: bindActionCreators(notePiano, dispatch),
});

const connectedPiano = connect(mapStateToProps, mapDispatchToProps)(Piano);

export default connectedPiano;
