import React from "react";
import { IMelodition } from "../../../../../_types/player.types";

interface CanvasProps {
  Melo: IMelodition;
}

const GameBack: React.FC<CanvasProps> = ({ Melo }) => {
  const drawBackContext = (ref: HTMLCanvasElement | null) => {
    if (ref && Melo.canvasSize && Melo.gameLoading) {
      const backCtx = ref.getContext("2d")!;
      backCtx.clearRect(0, 0, Melo.canvasSize.width, Melo.canvasSize.height);
      const keyRefs = Melo.pianoSize;
      const length = keyRefs ? keyRefs.length - 1 : 61;
      const lineWidth = keyRefs ? keyRefs[length].width : 0.5;
      const h = Melo.canvasSize.height;
      keyRefs?.forEach((rect, i) => {
        backCtx.beginPath();
        const x = rect.left;
        if (rect.key === "CF") {
          backCtx.moveTo(x, 0);
          backCtx.lineTo(x, h);
          backCtx.lineWidth = lineWidth;
          backCtx.strokeStyle = "rgba(50,50,50,0.8)";
          backCtx.stroke();
        } else if (rect.key === "last") {
          backCtx.moveTo(x, 0);
          backCtx.lineTo(x, h);
          backCtx.lineWidth = lineWidth;
          backCtx.strokeStyle = "rgba(50,50,50,0.8)";
          backCtx.stroke();
        } else if (rect.key === "black") {
          backCtx.rect(x, 0, rect.width, h);
          backCtx.fillStyle = "rgba(0,0,0,0.4)";
          backCtx.fill();
        }
      });
    }
  };
  return (
    <canvas
      ref={(ref) => drawBackContext(ref)}
      width={Melo.canvasSize?.width}
      height={Melo.canvasSize?.height}
      style={{ zIndex: 1 }}
    />
  );
};
export default GameBack;
