import React from "react";
import {
  Grid,
  makeStyles,
  Select,
  Slider,
  Switch,
  Theme,
} from "@material-ui/core";
import { MeloAssigner } from "../../../_actions/player.actions";
import { VolumeDown, VolumeUp } from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "flex",
    justifyItems: "baseline",
  },
  selectVol: {
    padding: theme.spacing(1, 2),
    height: 30,
    width: 180,
    "& > div > span": {
      padding: "0 !important",
    },
  },
  selectInput: {
    paddingTop: theme.spacing(1),
    height: 30,
    width: 80,
  },
}));

interface MetronomeProps {}

export const Metronome: React.FC<MetronomeProps> = () => {
  const classes = useStyles();
  const [on, setOn] = React.useState(true);

  const handleSound = (event: React.ChangeEvent<{ value: unknown }>) => {
    MeloAssigner({ metTrack: event.target.value });
  };
  const handleVolume = (event: any, newValue: any) => {
    MeloAssigner({ metVol: newValue / 100 });
  };
  const handleOn = () => {
    setOn((p) => {
      MeloAssigner({ metOn: !p });
      return !p;
    });
  };
  return (
    <div className={classes.root}>
      <Switch
        checked={on}
        onChange={handleOn}
        name="Metronome"
        inputProps={{ "aria-label": "Metronome switch" }}
      />
      <Grid container spacing={2} className={classes.selectVol}>
        <Grid item>
          <VolumeDown />
        </Grid>
        <Grid item xs>
          <Slider
            color="secondary"
            onChangeCommitted={handleVolume}
            defaultValue={75}
            min={0.1}
            aria-labelledby="continuous-slider"
          />
        </Grid>
        <Grid item>
          <VolumeUp />
        </Grid>
      </Grid>
      <Select
        native
        label="Metronome Sound"
        color="secondary"
        inputProps={{
          name: "Metronome Sound",
          id: "outlined-age-native-simple",
        }}
        onChange={handleSound}
        className={classes.selectInput}
      >
        <option value={0}>One</option>
        <option value={1}>Two</option>
        <option value={2}>Three</option>
      </Select>
    </div>
  );
};
