import React from "react";
import { canvasWheel } from "../../../../../_actions/player.actions";
import { ICanvas } from "../../../../../_types/player.types";

const SvgLine: React.FC<ICanvas> = ({ width, baseLine }) => {
  return (
    <svg
      width={width + 7}
      height={baseLine}
      onWheel={(e) => canvasWheel(e)}
      style={{
        bottom: 0,
        left: -7,
        zIndex: 3,
        position: "absolute",
        filter: "drop-shadow(rgba(0, 0, 0, 0.7) 0px 1px 1px)",
      }}
    >
      <style>{"@keyframes draw{to{stroke-dashoffset:0}}"}</style>
      <polygon points={"0,0 10,8 0,16"} fill={"red"} />
      <line
        style={{
          strokeDasharray: width + 7,
          strokeDashoffset: width + 7,
          animation: "draw 1s linear forwards",
        }}
        x1={0}
        y1={8}
        x2={width + 7}
        y2={8}
        strokeWidth={2.5}
        stroke={"red"}
      />
    </svg>
  );
};
export default SvgLine;
