import React from "react";
import { makeStyles, Slider, Theme, Typography } from "@material-ui/core";
import {
  MeloAssigner,
  MeloMusicVolume,
} from "../../../_actions/player.actions";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(1, 2),
  },
}));

export const SideVolume: React.FC = () => {
  const classes = useStyles();

  const handlePlayVolume = (_: any, newValue: any) => {
    MeloAssigner({ playVolume: newValue / 100 });
  };
  const handleMusicVolume = (_: any, newValue: any) => {
    MeloMusicVolume(newValue / 100);
  };
  return (
    <div className={classes.root}>
      <Typography gutterBottom variant="caption">
        Music Volume
      </Typography>
      <Slider
        color="secondary"
        defaultValue={75}
        min={0.1}
        onChangeCommitted={handleMusicVolume}
        aria-labelledby="musicvolume-slider"
      />
      <Typography gutterBottom variant="caption">
        Play Volume
      </Typography>
      <Slider
        color="secondary"
        defaultValue={75}
        min={0.1}
        onChangeCommitted={handlePlayVolume}
        aria-labelledby="playvolume-slider"
      />
    </div>
  );
};
