import React, {
  useState,
  forwardRef,
  useImperativeHandle,
  useCallback,
} from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import Slider from "@material-ui/core/Slider";
import { AppActions, AppState } from "../../../_types";
import { canvasWheel, drawSeekBar } from "../../../_actions/player.actions";
import { IMelodition } from "../../../_types/player.types";

type SeekBarProps = ReturnType<typeof mapStateToProps> &
  ReturnType<typeof mapDispatchToProps>;

const Seekbar: React.FC<SeekBarProps> = ({ Melo, drawSeekBar }) => {
  const callback = useCallback(
    (node: any) => {
      if (node && Melo.seekLoading) {
        drawSeekBar({ seekBar: node });
      }
      return () => {};
    },
    [Melo.seekLoading, drawSeekBar]
  );

  return (
    <div style={{ height: "40%" }}>
      <SeekInput ref={callback} Melo={Melo} />
    </div>
  );
};
const mapStateToProps = (state: AppState) => ({
  Melo: state.melodition,
});
const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  drawSeekBar: bindActionCreators(drawSeekBar, dispatch),
});
const connectedSeekbar = connect(mapStateToProps, mapDispatchToProps)(Seekbar);

export default connectedSeekbar;

interface SeekInputProps {
  Melo: IMelodition;
}
const SeekInput = forwardRef<unknown, SeekInputProps>(({ Melo }, ref) => {
  const [seek, setSeek] = useState(0);
  useImperativeHandle(ref, () => {
    return {
      changeSeeker: changeSeeker,
    };
  });
  const changeSeeker = (value: number) => {
    if (typeof value === "number") {
      setSeek(value);
    }
  };
  const seekchanger = (value: number | number[]) => {
    if (typeof value === "number") {
      canvasWheel(value);
    }
  };
  return (
    <Slider
      color="secondary"
      orientation="vertical"
      aria-label="seekbar"
      defaultValue={0}
      max={Melo.songLength}
      min={0}
      onChange={(_, e) => seekchanger(e)}
      aria-labelledby="vertical-slider"
      value={seek}
    />
  );
});
