export const pitchName = (pitch: number) => {
    let n = pitch % 12;
    let t = "C";
    if (n === 1) {
        t = "Db";
    }
    if (n === 2) {
        t = "D";
    }
    if (n === 3) {
        t = "Eb";
    }
    if (n === 4) {
        t = "E";
    }
    if (n === 5) {
        t = "F";
    }
    if (n === 6) {
        t = "Gb";
    }
    if (n === 7) {
        t = "G";
    }
    if (n === 8) {
        t = "Ab";
    }
    if (n === 9) {
        t = "A";
    }
    if (n === 10) {
        t = "Bb";
    }
    if (n === 11) {
        t = "B";
    }
    return "" + t;
};
export const outLine = (v: number) => v === 101 || v === 81 || v === 60 || v === 40;

const emulatedKeys = {
    z: 48,
    s: 49,
    x: 50,
    d: 51,
    c: 52,
    v: 53,
    g: 54,
    b: 55,
    h: 56,
    n: 57,
    j: 58,
    m: 59,
    ",": 60,
    l: 61,
    ".": 62,
    ";": 63,
    "/": 64,
    q: 65,
    2: 66,
    w: 67,
    3: 68,
    e: 69,
    4: 70,
    r: 71,
    t: 72,
    6: 73,
    y: 74,
    7: 75,
    u: 76,
    i: 77,
    9: 78,
    o: 79,
    0: 80,
    p: 81,
    "-": 82,
    "[": 83,
    "]": 84,
}

type IPianoEvent = React.TouchEvent<SVGGElement> | React.MouseEvent<SVGGElement, MouseEvent>
export class PianoEvent {
    isDown = false
    playedKey: number | string | null;
    keyboardUp = (e: KeyboardEvent) => {
        e.preventDefault();
        const key = e.key as keyof typeof emulatedKeys;
        if (emulatedKeys.hasOwnProperty(key)) {
            const note = emulatedKeys[key];
            midiNoteOff(note);
            this.playedKey = null;
        }
    };
    keyboardDown = (e: KeyboardEvent) => {
        e.preventDefault();
        const key = e.key as keyof typeof emulatedKeys;
        if (emulatedKeys.hasOwnProperty(key)) {
            if (key !== this.playedKey) {
                const note = emulatedKeys[key];
                midiNoteOn(note);
                this.playedKey = key;
            }
        }
    };
    mouseUpReset = () => {
        this.isDown = false;
    };
    mouseDown = (e: IPianoEvent) => {
        e.preventDefault();
        const ref: SVGGElement = e.currentTarget;
        const note = ref.tabIndex;
        this.isDown = true;
        this.playedKey = note;
        midiNoteOn(note);
    };
    mouseOver = (e: IPianoEvent) => {
        e.preventDefault();
        const ref: SVGGElement = e.currentTarget;
        const note = ref.tabIndex;
        if (this.isDown && note !== this.playedKey) {
            this.playedKey = note;
            midiNoteOn(note);
        }
    };
    mouseLeave = (e: IPianoEvent) => {
        e.preventDefault();
        const ref: SVGGElement = e.currentTarget;
        const note = ref.tabIndex;
        if (this.isDown) {
            midiNoteOff(note);
            this.playedKey = null;
        }
    };
    mouseUp = (e: IPianoEvent) => {
        e.preventDefault();
        const ref: SVGGElement = e.currentTarget;
        const note = ref.tabIndex;
        this.isDown = false;
        midiNoteOff(note);
        this.playedKey = null;
    };
}