import React, { useEffect, useState } from "react";
import { IconButton } from "@material-ui/core";
import { PauseCircleOutline, PlayCircleOutline } from "@material-ui/icons";
import { startPlayer, stopPlayer } from "../../../_actions/player.actions";

interface IPlayProps {
  loading: boolean;
}
export const PlayButton: React.FC<IPlayProps> = ({ loading }) => {
  const [onAir, setOnAir] = useState(false);

  const playHandler = () => {
    if (onAir) {
      stopPlayer();
      setOnAir(false);
    } else {
      startPlayer();
      setOnAir(true);
    }
  };
  const keyboardUp = (e: KeyboardEvent) => {
    if (e.code === "Space" && !loading) {
      playHandler();
    }
  };
  useEffect(() => {
    document.addEventListener("keyup", keyboardUp);
    return () => {
      document.removeEventListener("keyup", keyboardUp);
    };
  });
  return (
    <IconButton
      id="play-button"
      color="secondary"
      aria-label="play song"
      onClick={playHandler}
    >
      {onAir ? (
        <PauseCircleOutline style={{ fontSize: "1.8em" }} />
      ) : (
        <PlayCircleOutline style={{ fontSize: "1.8em" }} />
      )}
    </IconButton>
  );
};
